.btn {
  font-size: 1rem;
  font-weight: 700;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  line-height: 100%;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.btn__primary {
  border: 2px solid #E4C1AE;
  background: #E4C1AE;
  color: #ffffff;
}
.btn__primary:hover, .btn__primary:active {
  background: #ffffff;
  color: #E4C1AE;
}
.btn__primary:active {
  transform: translateY(4px);
}
.btn__close {
  background: transparent;
  border: unset;
  font-size: 2rem;
  color: #121212;
  cursor: pointer;
  width: 1.5rem;
  height: 1.5rem;
}
.btn__close svg {
  display: block;
  margin: 0 auto;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

h2 {
  line-height: 120%;
  font-size: 1.875rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.625rem;
  }
}

p {
  line-height: 150%;
  font-size: 1rem;
}

.app-section {
  margin-top: 3rem;
}
@media (min-width: 992px) {
  .app-section {
    margin-top: 6rem;
  }
}
.app-article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .app-article {
    flex-direction: row;
    width: 70%;
  }
}
.app-article__picture {
  width: 83.3333333333%;
}
.app-article__picture img {
  display: block;
}
@media (min-width: 576px) {
  .app-article__picture {
    width: 66.6666666667%;
  }
}
@media (min-width: 992px) {
  .app-article__picture {
    width: 58.3333333333%;
  }
}
.app-article__content {
  width: 83.3333333333%;
  margin-top: 1.5rem;
}
@media (min-width: 576px) {
  .app-article__content {
    width: 66.6666666667%;
  }
}
@media (min-width: 992px) {
  .app-article__content {
    width: 41.6666666667%;
    margin-left: 3rem;
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .app-article__content {
    margin-left: 6rem;
  }
}
.app-article__description {
  margin-top: 1.5rem;
  margin-bottom: 2rem;
  font-weight: 300;
  text-align: justify;
}
@media (min-width: 992px) {
  .app-article__description {
    text-align: unset;
  }
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background: #ffffff;
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}